@import "./mixins";

// XX LARGE

.heading-xx-large {
	@include basic-properties(700, 40px, 52px);
}

.heading-xx-large-secondary {
	@include secondary;

	@extend .heading-xx-large;
}

.heading-xx-large-bold {
	@include bold;

	@extend .heading-xx-large;
}

.heading-xx-large-secondary-bold {
	@extend .heading-xx-large;
	@extend .heading-xx-large-bold;
	@extend .heading-xx-large-secondary;
}

// X LARGE

.heading-x-large {
	@include basic-properties(700, 36px, 44px);
}

.heading-x-large-secondary {
	@include secondary;

	@extend .heading-x-large;
}

.heading-x-large-bold {
	@include bold;

	@extend .heading-x-large;
}

.heading-x-large-secondary-bold {
	@extend .heading-x-large;
	@extend .heading-x-large-bold;
	@extend .heading-x-large-secondary;
}

