:root {
  --basic-font: system-ui, "Segoe UI", roboto, helvetica, arial, sans-serif,
  	"Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --always-dark: #000;
  --always-white: #fff;
  --bg: #18191a;
  --overlay: #282828;
  --overlay-shadow: 0 1px 2px rgb(0 0 0 / 10%);
  --text-primary: #f1f1f1;
  --text-secondary: #bcbcbc;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.hidden-input {
  appearance: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 0;
}

.unselectable {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
  -webkit-touch-callout: none;
}

