.footer {
  padding: 50px 20px;
  width: 100%;
}

.footer__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 25px;
}

.footer__social-list {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  gap: 20px;
}

.footer__social-item {
  width: 40px;
  height: 40px;
}

.footer__social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.footer__social-icon {
  color: var(--text-primary);
}
.footer__social-icon:hover {
  color: var(--text-secondary);
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
}

.main {
  display: flex;
  align-items: center;
  justify-content: center;
}

.body {
  min-height: 100vh;
  background-color: var(--bg);
}

.body-with-header-footer {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
}