@import "./mixins";

// LARGE

.label-large {
	@include basic-properties(500, 18px, 24px);
}

.label-large-secondary {
	@include secondary;

	@extend .label-large;
}

.label-large-regular {
	@include regular;

	@extend .label-large;
}

.label-large-secondary-regular {
	@extend .label-large;
	@extend .label-large-regular;
	@extend .label-large-secondary;
}

// MEDIUM

.label-medium {
	@include basic-properties(500, 16px, 20px);
